import React, { Component, useEffect } from 'react';
import axios from 'axios';
import { RootStateOrAny, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { useWallet } from '@binance-chain/bsc-use-wallet';

const BASE_URL = "https://my-json-server.typicode.com/themeland/netstorm-json/explore";

const initData = {
    preheading: "Most Popular",
    heading: "Popular Collections",
    btnText: "Explore More",
}



function MyCollection() {
    const {account } = useWallet();
    const history = useHistory();
    useEffect(() => {
        if(account === null){
            history.push('/connect');
        }
    })
    const collections  = useSelector((state: RootStateOrAny) => state.marketplace.collections).filter(m => m.creator === account)
    return (
        <section className="popular-collections-area">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        {/* Intro */}
                        <div className="intro d-flex justify-content-between align-items-end m-0">
                            <div className="intro-content">
                                <span>{initData.preheading}</span>
                                <h3 className="mt-3 mb-0">{initData.heading}</h3>
                            </div>
                            
                        </div>
                    </div>
                </div>
                <div className="row items">
                    {collections.map((item, idx) => {
                        return (
                            <div key={`cd_${idx}`} className="col-12 col-sm-6 col-lg-3 item">
                                <div className="card no-hover text-center">
                                    <div className="image-over">
                                        <Link to={`/collection/${item.address}`}>
                                            <img className="card-img-top" src={item.uri} alt="" />
                                        </Link> 
                                        {/* Seller */}
                                        
                                    </div>
                                    {/* Card Caption */}
                                    <div className="card-caption col-12 p-0">
                                        {/* Card Body */}
                                        <div className="card-body mt-4">
                                            <Link to={`/collection/${item.address}`}>
                                                <h5 className="mb-2">{item.symbol}</h5>
                                            </Link>
                                            <span>{item.name}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
}

export default MyCollection;